}

.btn {
  display: inline-block;
  padding: 20px 30px;
  font-weight: 600;
  font-size: 16px;
  line-height: 1;
  font-family: "Inter", sans-serif;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background: #36BCBB;
  border: 1px solid #36BCBB;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn:disabled,
.btn.disabled {
  cursor: not-allowed !important;
}

.btn:not(:disabled):hover {
  color: #36BCBB;
  background-color: #ffffff;
}

.btn-blue {
  color: #0081C8;
  background-color: #ffffff;
  border-color: #ffffff;
}

.btn-blue:hover {
  color: #ffffff;
  background-color: transparent;
}

.btn-o {
  font-weight: 500;
  color: #3C3C3C;
  background-color: transparent;
}

.btn-o:hover {
  color: #ffffff;
  background-color: #36BCBB;
}

.btn-white-o {
  font-weight: 500;
  color: #ffffff;
  background-color: transparent;
  border-color: #ffffff;
}

.btn-white-o:hover {
  color: #3C3C3C;
  background-color: #ffffff;
}

.link-arrow {
  position: relative;
  padding-right: 40px;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.22;
  font-family: "Inter", sans-serif;
  color: #6D6D6D;
  text-decoration: none;
}

.link-arrow::after {
  content: "";
  position: absolute;
  top: 3px;
  right: 0;
  display: inline-block;
  width: 24px;
  height: 15px;
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='15' viewBox='0 0 24 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M23.7071 8.20711C24.0976 7.81658 24.0976 7.18342 23.7071 6.7929L17.3431 0.428934C16.9526 0.0384093 16.3195 0.0384092 15.9289 0.428934C15.5384 0.819458 15.5384 1.45262 15.9289 1.84315L21.5858 7.5L15.9289 13.1569C15.5384 13.5474 15.5384 14.1805 15.9289 14.5711C16.3195 14.9616 16.9526 14.9616 17.3431 14.5711L23.7071 8.20711ZM-8.74228e-08 8.5L23 8.5L23 6.5L8.74228e-08 6.5L-8.74228e-08 8.5Z' fill='%2336BCBB'/%3E%3C/svg%3E%0A");
