.link-arrow:hover::after {
  right: -3px;
}

.charity__btn-wrap .link-arrow:hover::after {
  right: -3px;
}

.link {
  position: relative;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  font-family: "Inter", sans-serif;
  text-align: center;
  color: #3C3C3C;
  text-decoration: none;
}

.link::after {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 0;
  display: block;
  width: 100%;
  height: 2px;
  background-color: #36BCBB;
  transition: all 0.3s ease;
}

.link:hover::after {
  bottom: -7px;
}

