@keyframes fade {
  from {
    opacity: 0.1;
  }

  to {
    opacity: 1;
  }
}

@keyframes move-light {
  from {
    transform: translateX(-10em) skewX(-45deg);
  }

  to {
    transform: translateX(40em) skewX(-45deg);
  }
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 33px 15px;
  background-color: #fff;
}

.header__logo {
  margin-right: 29px;
}

.header__nav {
  flex-grow: 1;
}

.header__nav ul {
  display: flex;
  align-items: center;
  justify-content: center;
}

.header__nav ul li {
  padding: 10px 12.5px;
}

.header__nav ul a {
  position: relative;
  display: inline-block;
  font-family: "Inter", sans-serif;
  font-weight: 600;
  font-size: 16px;
  color: #6D6D6D;
  text-decoration: none;
  transition: all .3s ease;
}

.header__nav ul a:before {
  transition: all .3s ease;
  width: 100%;
  height: 2px;
  background-color: transparent;
  content: '';
  position: absolute;
  display: block;
  bottom: -2px;
  left: 0;
}

.header__nav ul a:hover {
  color: #36BCBB;
}

.header__nav ul a:hover:before {
  background-color: #36BCBB;
}

.header__cta {
  margin: 0 35px;
  padding: 14px 20px;
  font-size: 14px;
}

.header__burger {
  margin-left: 40px;
  width: 41px;
  height: 37px;
  background-color: #fff;
  border: 0;
  appearance: none;
  background-image: url("data:image/svg+xml,%3Csvg width='41' height='37' viewBox='0 0 41 37' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cline x1='0.108643' y1='8.0271' x2='40.1086' y2='8.0271' stroke='%233C3C3C' stroke-width='2'/%3E%3Cline x1='0.108643' y1='18.0271' x2='40.1086' y2='18.0271' stroke='%233C3C3C' stroke-width='2'/%3E%3Cpath d='M0.108643 28.0271H23.6086' stroke='%233C3C3C' stroke-width='2'/%3E%3C/svg%3E%0A");
}

.header__cta-mobile {
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  width: 100%;
  display: block;
  max-width: 294px;
  padding: 15px;
}

.header__nav-close {
  position: absolute;
  top: 20px;
  right: 32px;
  width: 32px;
