.cta {
  padding: 80px 0 105px;

  @media (max-width: 767px) {
    padding: 53px 0 50px;
  }

  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    @media (max-width: 1023px) {
      flex-direction: column;
      max-width: 500px;
      margin: 0 auto;
    }
  }

  &__title {
    max-width: 480px;
    margin-right: 30px;
    font-weight: bold;
    font-size: 46px;
    line-height: 54px;
    font-family: $add;
    letter-spacing: -0.3px;

    @media (max-width: 1199px) {
      margin-bottom: 40px;
      font-size: 38px;
      line-height: 42px;
    }

    @media (max-width: 767px) {
      margin-bottom: 40px;
      font-size: 26px;
      line-height: 33px;
    }
  }

  &-title-section {
    padding: 73px 0;

    .cta__title {
      max-width: 800px;
    }
  }

  &__form-wrap {
    max-width: 552px;
    margin-top: -9px;
    padding-right: 26px;

    @media (max-width: 767px) {
      padding-right: 0;
    }

    &__contacts {
      width: 50%;
      max-width: 362px;
    }
  }

}
.cta-partnership {
  padding: 58px 0 50px;

  .cta-contacts {
    padding-top: 8px;
    @media screen and (max-width: 1023px) {
      padding-top: 80px;
    }
  }
}

.cta-form {
  &__input {
    display: block;
    width: 100%;
    margin-bottom: 30px;
    padding: 10px 0;

    font-weight: normal;
    font-size: 16px;
    line-height: 1.3;
    font-family: $reg;
    color: $dark-gray;
    background: transparent;
    border: 0;
    border-bottom: 1px solid #aaaaaa;
    outline: none;
    transition: all 0.3s ease;

    &:focus {
      border-color: $accent;
    }

    &::placeholder {
      color: $dark-gray;
    }

    @media (max-width: 767px) {
      margin-bottom: 23px;
    }
  }

  &__textarea {
    height: 84px;
    resize: none;

    @media (max-width: 767px) {
      height: 70px;
    }
  }

  &__footer {
    display: flex;
    margin-top: 40px;

    @media (max-width: 767px) {
      flex-direction: column;
      text-align: center;
    }
  }

  &__btn {
    min-width: 235px;
    margin-right: 40px;

    @media (max-width: 767px) {
      margin-right: 0;
      margin-bottom: 18px;
    }
  }

  &__agreement {
    font-weight: normal;
    font-size: 12px;
    line-height: 15px;
    font-family: $reg;
    color: #737373;

    a {
      color: #737373;
      text-decoration: underline;
    }
  }
}

.cta-contacts {
  padding-top: 100px;

  &_flex {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 100%;
    margin-bottom: 83px;
    padding-top: 0;

    @media (max-width: 1024px) {
      flex-direction: column;
      margin-bottom: 0;
    }
  }

  &__title {
    margin-bottom: 32px;
  }

  &__row {
    margin-bottom: 32px;
  }

  &__heading {
    margin-bottom: 10px;

    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    font-family: $reg;
    color: $dark-gray;
  }

  &__link {
    margin-right: 30px;

    font-weight: bold;
    font-size: 26px;
    font-family: $reg;
    color: $graphite;
    text-decoration: none;
    transition: all 0.3s ease;

    @media (max-width: 1024px) {
      font-size: 20px;
    }

    &:hover {
      color: $accent;
    }
  }

  &__small {
    margin-top: 10px;

    font-weight: normal;
    font-size: 14px;
    font-family: $reg;
    color: $graphite;
  }
}
